.App {
  text-align: center;
}
* {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow-x: hidden;
  text-decoration: none;
}
::-webkit-scrollbar {
  display: none;
}
.header {
  width: 100%;
  height: 65px;
  display: block;
  background-color: #333333;
}
.naxv {
  float: right;
  height: 100%;
}

.naxv a {
  height: 100%;
  display: table;
  float: left;
  padding: 0px 20px;
}
.naxv a li {
  display: table-cell;
  vertical-align: middle;
  height: 100%;
  color: white;
  font-family: sans-serif;
  font-size: 16px;
}

.ih {
  width: 1000px;
  height: 100%;
  display: block;
  margin: 0 auto;
}
.logo {
  height: 100%;
  display: table;
  float: left;
  color: white;
}
.logo h1 {
  /*
    color:white;
    vertical-align: middle;
    font-size: 32px;
    font-weight: 200;
    font-family: sans-serif;*/
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  color: white;
  font-family: sans-serif;
  font-size: 32px;
}
.h1 link {
  color: white;
}
